.pricing {
  margin: 0 auto 3rem;
}

.pricing section {
  border: 2px solid #cca000;
  margin: 0 auto 6rem;
  max-width: 25rem;
  padding: 4.5rem 3rem;
  text-align: center;
}

.pricing section .h1 del {
  color: #999;
  display: inline-block;
  font-size: .75em;
  font-weight: 400;
  margin-right: .25rem;
  position: relative;
  text-decoration: none;
}

.pricing section .h1 del::after {
  background: #999;
  content: "";
  height: 2px;
  left: -.25rem;
  margin-top: -1px;
  position: absolute;
  right: -.25rem;
  top: 50%;
}

@media screen and (max-width: 30rem) {
  .pricing section .h1 del {
    display: none;
  }
}

.sale {
  font-weight: 700;
}

.pricing section .h1 small {
  display: inline-block;
  font-size: 1.5rem;
  font-weight: 700;
}

.pricing section p:not(:last-child) {
  margin-bottom: 1.5rem;
}

.pricing .vat {
  font-size: .75rem;
}

.pricing .cta {
  align-items: center;
  display: flex;
  justify-content: center;
  margin: 0 auto;
  max-width: 12rem;
}

.pricing .cta svg {
  margin-right: 1rem;
}

.pricing .cta svg * {
  fill: #f0c674;
}

.pricing h2 small {
  display: block;
  font-weight: 400;
  letter-spacing: 0;
  padding-top: .25rem;
}

.addons {
  --columns: 1;
  display: grid;
  grid-gap: 3rem;
  grid-template-columns: repeat(var(--columns), 1fr);
  margin-bottom: 9rem;
}

@media (min-width: 38em) {
  .addons {
    --columns: 2;
  }
}

@media (min-width: 56em) {
  .addons {
    --columns: 4;
  }
}

.addons p {
  color: rgba(60, 67, 80, 0.8);
}

.addons p a {
  color: #000;
  text-decoration: underline;
}

.addons p strong {
  color: #000;
  font-weight: 500;
}

.faq {
  -webkit-column-gap: 3rem;
          column-gap: 3rem;
  -webkit-columns: 1;
          columns: 1;
}

@media (min-width: 56em) {
  .faq {
    -webkit-columns: 2;
            columns: 2;
  }
}

.faq li {
  margin: 1.5rem 0;
}

.faq .text table {
  border: 1px solid #ccc;
}

